.App-header {
  /*background-color: #282c34;*/
  /*min-height: 100vh;*/

  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 1px 10px 18px 7px #000;
}
.App-header img{
  width: calc(30vmin);
}
.App main {
  max-width: 1200px;
  margin: 0 auto;
  padding-top:  5vmin;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: calc(10px + 2vmin);
  text-align: center;
  font-size: calc(10px + 2vmin);
  color: white;

}
.Rodada {
  display: grid;
  grid-template-columns: 2fr 1fr;
  
  column-gap: 20px;
 
}
.Rodada-count {
  border-style: solid;
  border-width: calc(0.3vmin);
  border-color: #c7c3cf;
  border-radius: 2px;

}
.Reserva {
  display: grid;
  justify-content: center;
  
  width: calc(100px + 20vmin);
  border-style: solid;
  border-width: calc(0.3vmin);
  border-color: #c7c3cf;
  border-radius: 2px;
  row-gap: calc(10px + 2vmin);
}
.Reserva button{
  border: none;
  background-color:transparent;
  color: #c7c3cf;
}
.Reserva-movel{
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 2fr 1fr ;
  width: calc(100px + 20vmin);
  column-gap: 20px;
  font-size: calc(40px + 5vmin);
  align-items: center;
}
.Reserva-movel button{
  font-size: calc(50px + 3vmin);
}
.Reserva-conquista{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  border-style: solid solid none solid;
  column-gap: 5px;
  font-size: calc(20px + 3vmin);
  text-align: center;
  align-items: center;
}
.Reserva-conquista button{
  font-size: calc(20px + 3vmin);
}

.Fortaleza{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  border-style: solid solid none solid;
  column-gap: 5px;
  font-size: calc(20px + 3vmin);
  text-align: center;
  align-items: center;
}
.Fortaleza button{
  font-size: calc(20px + 3vmin);
}

/*deixar responsivo*/

@media (max-width: 300px) {
    #app {
        flex-direction: column;
    }
    #app main {
        margin-top: 30px;
        margin-left: 0;
    }
    
    #app aside {
        width: 100%;
    }
}
@media (max-height: 360px){
  .App main{
    flex-direction: row;
    column-gap: calc(10px + 2vmin);
  }

}